/*ESTILOS PRINCIPALES DE CONTENEDORES DEL DASHBOARD*/
body{
    background-color: #1a9bfcd9;
}
.bodyContent{
    background-color: #e0e0e0;
   width: 100vw;
    height: 93vh;
}
.contenedorCompleto {
    margin-top: 65px;
    background-color: #e0e0e0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
    /* Establece una altura fija para el div */
    flex-direction: column;
}

.containerForm {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    /* Establece una altura fija para el div */
    flex-direction: column;
    width: 100%;
}

.containerOptions {

    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    /* Establece una altura fija para el div */
    flex-direction: column;

    width: 100%;
}

.containerOcultos {

    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    /* Establece una altura fija para el div */
    flex-direction: column;
    width: 100%;
}

.table_container {
  
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


/*ESTILOS DE LOS TARGEST PARA MOSTRAR INFO DEL DASHBOARD*/
.contenedorComponenteInfo {

    width: 80%;
    display: flex;
    align-items: center;
    flex-direction: column;
    align-content: center;

}

.cont_title_info_paciente {
 
    width: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
    
}

.containerTargerts {
   
    width: 70%;
    
}

.target {
 
    width: 100%;
    text-align: center;
    box-sizing: border-box;
   padding: 10px;
    border: 1px solid white;
    box-shadow: 12px 17px 51px rgba(0, 0, 0, 0.22);
  margin-top: 15px;
    border-radius: 17px;
    text-align: center;
    cursor: pointer;
    transition: all 0.5s;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    font-weight: bolder;
    color: black;
    flex-direction: column;
    
}
.target:hover {
    border: 1px solid black;
    transform: scale(1.05);
    background-color: rgba(255, 255, 255, 0.897);

  }
  
  .target:active {
    transform: scale(0.95) rotateZ(1.8deg);
  }

  .target:hover h2 {
    color: #2e2e2e;
  }
  .target:hover .contTitleTarget {
    background-color: #84c8ffa9;
    width: 100%;
    border-radius: 17px 17px 0px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.22);
  }




