/*-------------------------------------STYLE DE COMPONENTES---------------------------------------------------------*/
/*BUTTONS*/
.btn_VerOpciones{
    width: 200px;
    
    appearance: none;
 background-color: transparent;
 border: 0.125em solid #1A1A1A;
 border-radius: 0.6em;
 box-sizing: border-box;
 color: #3B3B3B;
 cursor: pointer;
 display: inline-block;
 font-family: Roobert,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
 font-size: 16px;
 font-weight: 600;
 line-height: normal;
 margin: 8px;
 min-height: 1em;
 min-width: 0;
 outline: none;
 padding: 0.7em ;
 text-align: center;
 text-decoration: none;
 transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
 user-select: none;
 -webkit-user-select: none;
 touch-action: manipulation;
 will-change: transform;
}

.btn_VerOpciones:disabled {
    pointer-events: none;
   }
   
   .btn_VerOpciones:hover {
    color: #fff;
    background-color: #1A1A1A;
    box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
    transform: translateY(-2px);
   }
   
   .btn_VerOpciones:active {
    box-shadow: none;
    transform: translateY(0);
   }

   /*btn ocultos*/
   
   .btn_Oculto {
    padding: 1.3em 1em;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    color: #000;
    background-color: #fff;
    border: none;
    border-radius: 5px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
    margin: 13px;
  }
  
  .btn_Oculto:hover {
    background-color: #23c483;
    box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
    color: #fff;
    transform: translateY(-7px);
  }
  
  .btn_Oculto:active {
    transform: translateY(-1px);
  }


  /*TABLE*/
  table{
    background-color: #fafafa;
    transition: all 0.5s;
    margin-top: 30px;
    margin-bottom: 30px;
   
  }
  th{
    background-color: #1a9bfcd9;
    

  }
  td{
    background-color: #ffffff;
    cursor: pointer;
    scale: 1.05;
  }
  .fila:hover{
    scale: 1.05;
  }

  tr{
    background-color: #383838;
    text-align: center;
    transition: all 0.5s;

  }

  .titleValor{
    color: #050508;
    margin: 12px;
  }
  .value{
    color: #1e2031;
    margin-top: 12px ;
  }
  h2{
    color: #1c1c22;
  }
  h1{
    color: #343541;
  }
  .Icons{
    width: 40px;
    margin-right: 15px;
 
  }
  .contTitleTarget{
    display: flex;
        flex-direction: row;
       justify-content:flex-start;
    align-items: center;
    text-align: center;
    margin-bottom: 10px;
  }